<template>
  <div class="content">
    <div class=header>
      <span @click="close"><i class="el-icon-circle-close-outline"></i>关闭</span>
      <span>{{vin}}</span>
    </div>
    <div class="contentParent">
    <div class="contentBox" v-for="(item,index) in addList">
      <div class="block stylePadding">
        <span class="demonstration">质损部位</span>
        <el-cascader
          :options="options12"
          v-model="item.loseId">
        </el-cascader>
        <span style="float: right;padding-right: 40px;" @click="removeMassLoss(index)">
          <span class="icon-deletes"></span>
        </span>
      </div>
      <div class="stylePadding">
        <span style="display: inline-block;line-height: 75px">详细描述</span>
        <el-input
          type="textarea"
          :rows="3"
          resize="none"
          placeholder="请输入内容"
          v-model="item.reason">
        </el-input>
      </div>
      <div class="stylePadding">
        <el-alert
          title="最多允许上传1张照片"
          :closable="false"
          type="warning">
        </el-alert>
      </div>
      <div class="pictureStyle" @mouseover="getIndex(index)">
        <el-upload
          action="http://10.108.2.29:28060/api/file/uploadController/upload"
          list-type="picture-card"
          multiple
          ref="upload"
          :data="datas"
          :limit="1"
          :auto-upload="true"
          :file-list="addList[index].imgUrl"
          :on-success="pictureSuccess"
          :on-preview="handlePictureCardPreview"
          :on-exceed="onExceed"
          :on-remove="handleRemove"
          :before-upload="beforeAvatarUpload">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog title="查看图片" :visible.sync="dialogVisible" center>
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </div>
    </div>
    <div class="addMassLoss">
      <span @click="addMassLoss">添加质损部位</span>
    </div>
    </div>
    <div class="footer">
      <SolidButton :type='"solid"' @click="submits" :text='"提交"'></SolidButton>
      <SolidButton :type='"hollow"' @click="cancel" :text='"取消"'></SolidButton>
    </div>
  </div>
</template>

<script>
  import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
    export default {
      components: {
        SolidButton,
      },
        name: "mass-register",
        data(){
          return{
            conunts:0,
            addList:[
              {
                loseId:[],//选择的质损部位
                reason:"",//文本域
                imgUrl:[]//上传的图片
              }
            ],
            options12: [],
            // selectedOptions: [],//传给后台的质损部位
            fileList:[],
            orderId:"",//订单id
            // textarea: '',//质损信息备注
            datas:{   //上传图片时传递的token验证
              token:""
            },
            dialogImageUrl: '',
            dialogVisible: false,
            vin:"",//从签收页面传来的vin码
            saveType:""//判断提交时是新增还是编辑
          }
        },
        methods:{
        //删除质损部位
          removeMassLoss(index){
            var self=this;
            self.RemoveValByIndex(self.addList,index)
          },
          RemoveValByIndex(arr,index){
            arr.splice(index, 1);
          },
          //添加质损部位
          addMassLoss(){
            var self=this;
            var parms={
              loseId:[],//选择的质损部位
              reason:"",//文本域
              imgUrl:[]//上传的图片
            }
            if(self.addList.length==0){
              self.addList.push(parms);
              return;
            }
            for (var i=0;i<self.addList.length;i++){
              if(self.addList[i].loseId.length==0){
                this.$message.error("请填写质损信息");
                return;
              }
            }
            for (var i=0;i<self.addList.length;i++){
              if(self.addList[i].loseId.length!=0){
                self.addList.push(parms);
                return;
              }
            }
          },
        //提交
          submits(){
            var self=this;
            var sendCode=[];//往后台传递的list集合
            var parms= {
              id:null,
              parentId:"",
              losePositionId:"",
              reason:"",
              receiveId:self.orderId,
              loseImgUrl:""
            }
            if(self.addList.length==0){
              sendCode.push(parms);
              // return;
            }
            for(var i=0;i<self.addList.length;i++){
              var pictureList=[];//图片
              if(self.addList[i].imgUrl.length!=0){
                if(self.addList[i].imgUrl[0].hasOwnProperty("response")){
                  pictureList.push(self.addList[i].imgUrl[0].response[0])
                }else {
                  pictureList.push(self.addList[i].imgUrl[0].url)
                }
              }
              var parm={
                id:null,
                parentId:self.addList[i].loseId[0],
                losePositionId:self.addList[i].loseId[1],
                reason:self.addList[i].reason,
                receiveId:self.orderId,
                loseImgUrl:pictureList.join(",")
              }
              sendCode.push(parm)
            }

            // self.pictureList=[];
            // for(var i=0;i<self.fileList.length;i++){
            //   if(self.fileList[i].hasOwnProperty("response")){
            //     self.pictureList.push(self.fileList[i].response[0])
            //   }else {
            //     self.pictureList.push(self.fileList[i].url)
            //   }
            // }
            // var params={
            //   id:self.orderId,
            //   reason:self.textarea,
            //   loseImgUrl:self.pictureList.join(",")
            // }
            for (var i=0;i<self.addList.length;i++){
              if(self.addList[i].loseId.length==0){
                this.$message.warning("请填写质损信息");
                return;
              }
            }
            self.$api.post(self.saveType=="add" ? "crm/sign/editVehicleLose" : "crm/sign/updateVehicleLose",sendCode,success => {
              this.$message.success(success.repMsg);
              this.$router.push({name:"批量签收 ／ 批量签收"})
            },error => {
              this.$message.error(error.repMsg);
            })
          },
          beforeAvatarUpload(file) {
            console.log(file)
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt1M = file.size / 1024 / 1024 < 1
            if (!isJPG && !isPNG) {
              this.$confirm('上传图片暂时只支持JPG/PNG格式', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'error' ,
                callback: action => {
                }
              })
            }
            if (!isLt1M) {
              this.$confirm('上传图片大小不能超过 1MB!', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'error' ,
                callback: action => {
                }
              })
            }
            return isJPG || isPNG && isLt1M;
          },
          //图片上传成功时
          pictureSuccess(response, file, fileList){
            var self=this;
            this.$message.success("上传成功");
            self.addList[self.conunts].imgUrl=fileList
            // self.fileList=fileList;
          },
          getIndex(index){
            var self=this;
            self.conunts=index;
            // console.log(self.conunts)
          },
          //删除时
          handleRemove(file, fileList) {
            var self=this;
            // console.log(self.conunts)
            self.addList[self.conunts].imgUrl=[];
            // self.fileList=fileList;
          },
          //超出个数限制时
          onExceed(files, fileList){
            this.$message({
              message: '最多允许上传1张图片',
              type: 'error'
            });
          },
          //点击上传图片时
          submitUpload() {
            var self=this;
            this.$refs.upload.submit();
          },
          //查看大图时
          handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
          },
          //关闭按钮
          close(){
            var self=this;
            this.$router.push({name:"批量签收 ／ 批量签收"})
          },
          //取消按钮
          cancel(){
            var self=this;
            this.$router.push({name:"批量签收 ／ 批量签收"})
          }
        },
      created() {
        var self=this;
        self.datas.token=self.$api.sGetObject("_user").token;
        self.orderId=this.$route.query.id;//路由接收id
        self.vin=this.$route.query.vin;//路由接收id
        self.addList=[];
        self.$api.post("crm/sign/echoVehicleLose",{receiveId:self.orderId},success => {
          var parms= {
            id: null,
            reason: "",
            loseId: [],
            imgUrl: []
          }
          if(success.repData.length==0){
            self.addList.push(parms);
            self.saveType="add";
          }else {
            self.addList=success.repData;
            self.saveType="edit";
          }
        },error => {

        })
        self.$api.post("crm/sign/displayLoseName",{},success =>{
          self.options12=success.repData;
        },error =>{

        })

      }
    }
</script>

<style scoped>
  .content{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .header{
    line-height: 50px;
    border-bottom: 1px solid #f0f0f2;
  }
  .header span{
    width: 54px;
    display: inline-block;
    padding: 0 12px;
    cursor: pointer;
  }
  .header span i{
    font-size: 18px;
    vertical-align: middle;
    padding-right: 6px;
    margin-top: -4px;
  }
  .contentBox{
    padding: 20px 20px;
  }
  .footer{
    text-align: center;
    position: fixed;
    /*left: 50%;*/
    bottom: 40px;
    border-top: 1px solid #e5e5e5;
    width: calc(100% - 221px);
    padding-top: 30px;
    box-shadow:0 -6px 0 #f5f5f5;
  }
  .stylePadding{
    padding: 0px 0px 20px 0px;
  }
  .addMassLoss{
    text-align: center;
    padding-top: 40px;
  }
  .addMassLoss span{
    border: 1px solid #ececec;
    padding: 10px 34px;
    border-radius: 14px;
    cursor: pointer;
  }
  .contentParent{
    height: calc(100% - 154px);
    overflow-y: auto;
  }
</style>



